/* eslint-disable react/no-unused-state */
import React from 'react';
import Search from '@elliemae/ds-icons/Search';
import DSButton from '@elliemae/ds-basic/Button';
import DSTextBox from '@elliemae/ds-basic/form/TextBox';
import withSearchable from './withSearchable';

function SearchBoxImpl({
  containerProps,
  placeholder,
  className,
  disabled,
  disableButton,
  innerRef,
  showIcon,
  style,
  onChange,
  onKeyUp,
  onSearch,
  readOnly,
  value,
  clearable,
}) {
  return (
    <DSTextBox
      className={className}
      clearable={clearable}
      containerProps={containerProps}
      disabled={disabled}
      innerRef={innerRef}
      onChange={onChange}
      onKeyUp={onKeyUp}
      placeholder={placeholder}
      readOnly={readOnly}
      rightComponent={
        showIcon && (
          <DSButton
            disabled={disabled || disableButton}
            buttonType="text"
            icon={<Search />}
            iconSize="s"
            onClick={onSearch}
          />
        )
      }
      style={style}
      value={value}
    />
  );
}

export default withSearchable(SearchBoxImpl);
